 * Liveblog entry â€“ front and editor.
 */

.liveblog-entry {
	border-left: 4px solid #0073aa;
	padding-left: 20px;
	margin-bottom: 30px;
	position: relative;

	&.has-modified {
		border-left-color: #f0b849;
	}

	&.is-pinned {
		background: #f9f9f9;
		border-left-color: #d63638;
	}
}

.liveblog-entry__header {
	display: flex;
	gap: 12px;
	align-items: center;
	margin-bottom: 12px;
	font-size: 14px;
	color: #666;
}

.liveblog-entry__time {
	font-weight: 500;
}

.liveblog-entry__edited {
	font-style: italic;
	color: #f0b849;
}

.liveblog-entry__content {
	margin-top: 0;
}

.liveblog-entry-coauthors-list {
	list-style: none;
	margin: 0 0 8px 0;
	padding: 0;
}

.liveblog-entry-coauthor {
	display: inline-flex;
	align-items: center;
	gap: 6px;
	margin-right: 8px;
	margin-bottom: 4px;
}

.liveblog-entry-coauthor-remove {
	padding: 0 4px;
	cursor: pointer;
	background: transparent;
	border: none;
	color: #757575;
	font-size: 18px;
	line-height: 1;
}

.liveblog-entry-coauthor-remove:hover {
	color: #d63638;
}
